.app-navbar .button-container .menu-button.hvr-sweep-to-top:before {
  background-color: #F1F1F1; }

.app-navbar .button-container .menu-button.hvr-sweep-to-top:hover:before {
  -webkit-transform: scaleY(0);
  transform: scaleY(0); }

/**
 * Este bloque de código es importante para establecer el efecto de
 * la animación cuya clase es .hvr-sweep-to-top esto debido a que 
 * la transformación fue eliminada para el evento :hover desde el 
 * archivo hover.css, por lo que aquí se establece 
 * nuevamente y con mayor prioridad para evitar intervenir con 
 * otros eventos o estilos css.
 */
.app-navbar .button-container .menu-button.hvr-sweep-to-top:focus:before,
.app-navbar .button-container .menu-button.hvr-sweep-to-top:active:before {
  -webkit-transform: scaleY(1) !important;
  -moz-transform: scaleY(1) !important;
  -ms-transform: scaleY(1) !important;
  -o-transform: scaleY(1) !important;
  transform: scaleY(1) !important; }

.app-navbar .button-container button.menu-button:hover {
  background-color: #FFFFFF; }

.app-navbar .button-container ul.menu-dropdown {
  top: 80%;
